import React, { useState } from "react";
import { Link } from "react-router-dom";

function Form() {
  
  const [valid, setValid] = useState(false);

  const handleInputChange = () => {
    const inputs = document.querySelectorAll(".form__wrap__fio");
    let valid = true;
    inputs.forEach((input) => {
      if (!input.checkValidity()) {
        valid = false;
      }
    });
    setValid(valid);
  };

  return (
    <div className="form">
      <h1 className="form__title"></h1>
      <h2 className="form__secondTitle">  :</h2>
      <div className="form__wrap">
        <label>
          <input
            className="form__wrap__fio"
            placeholder=""
            type="text"
            pattern="^[--\s]+$"
            required
            onChange={handleInputChange}
          />
          <span className=""></span>
        </label>
        <label>
          <input
            className="form__wrap__fio"
            placeholder=""
            type="text"
            pattern="^[--\s]+$"
            required
            onChange={handleInputChange}
          />
          <span className=""></span>
        </label>
        <label>
          <input
            className="form__wrap__fio"
            placeholder=""
            type="text"
            pattern="^[--\s]+$"
            required
            onChange={handleInputChange}
          />
          <span className=""></span>
        </label>
        <label className="adressLabel">
          <input
            className="form__wrap__adress form__wrap__fio"
            placeholder=". , 3-  , . 51, . 303"
            type="text"
            pattern="^[--0-9\s\W]+$"
            required
            onChange={handleInputChange}
          />
          <span className=""> </span>
        </label>
        <label>
          <input
            className="form__wrap__fio"
            placeholder="+7 (999) 999 99-99"
            type="tel"
            pattern="^[0-9\+\-\s]+$"
            required
            onChange={handleInputChange}
          />
          <span className=""> </span>
        </label>
        <label>
          <input
            className="form__wrap__fio"
            placeholder="ivanov@mail.ru"
            type="email"
            required
            onChange={handleInputChange}
          />
          <span className="">E-mail</span>
        </label>
        <label>
          <input
            className="form__wrap__fio"
            placeholder="777000"
            type="text"
            pattern="[0-9]{6}"
            maxLength={6}
            required
            onChange={handleInputChange}
          />
          <span className=""></span>
        </label>
      </div>
      <Link to="/thankyou">
        <button
          type="submit"
          className="form__button"
          disabled={!valid}
        >
            
        </button>
      </Link>
    </div>
  );
}

export default Form;