import React, { useState } from "react";
import { Link } from "react-router-dom";

function SwiperPage() {
  const [activeSlide, setActiveSlide] = useState(3); //   

  return (
    <div className="mainSwiper">
      <h3 className="mainSwiper__title">:</h3>
      <svg
        className="mainSwiper__arrow"
        viewBox="0 0 64.00 64.00"
        fill="none"
        stroke="#108C3D"
      >
        <g id="SVGRepo_bgCarrier" stroke-width="0"></g>
        <g
          id="SVGRepo_tracerCarrier"
          strokeLinecap="round"
          strokeLinejoin="round"
          stroke="#CCCCCC"
          strokeWidth="1.024"
        ></g>
        <g id="SVGRepo_iconCarrier">
          <polyline points="24 40 32 32 24 24"></polyline>
          <polyline points="32 40 40 32 32 24"></polyline>
        </g>
      </svg>
      <div className="swiper">
        <div
          className={`swiper-slide${activeSlide === 0 ? " active" : ""}`}
          onMouseEnter={() => setActiveSlide(0)}
          onMouseLeave={() => setActiveSlide(3)}
        >
          <div className="swiper-green"></div>
          <h4 className="swiper-slide__title"></h4>
          <p className="swiper-slide__text">
               ,  , 
            ,  
          </p>
        </div>
        <div
          className={`swiper-slide${activeSlide === 1 ? " active" : ""}`}
          onMouseEnter={() => setActiveSlide(1)}
          onMouseLeave={() => setActiveSlide(3)}
        >
          <div className="swiper-green"></div>
          <h4 className="swiper-slide__title"></h4>
          <p className="swiper-slide__text">
                   
             
          </p>
        </div>
        <div
          className={`swiper-slide${activeSlide === 2 ? " active" : ""}`}
          onMouseEnter={() => setActiveSlide(2)}
          onMouseLeave={() => setActiveSlide(3)}
        >
          <div className="swiper-green"></div>
          <h4 className="swiper-slide__title">   </h4>
          <p className="swiper-slide__text">
              ,      
          </p>
        </div>
        <div
          className={`swiper-slide${activeSlide === 3 ? " active" : ""}`}
          onMouseEnter={() => setActiveSlide(3)}
          onMouseLeave={() => setActiveSlide(3)}
        >
          <div className="swiper-green"></div>
          <h4 className="swiper-slide__title">   </h4>
          <p className="swiper-slide__text">
              ,  3-4  
          </p>
        </div>
        <div
          className={`swiper-slide${activeSlide === 4 ? " active" : ""}`}
          onMouseEnter={() => setActiveSlide(4)}
          onMouseLeave={() => setActiveSlide(3)}
        >
          <div className="swiper-green"></div>
          <h4 className="swiper-slide__title"> </h4>
          <p className="swiper-slide__text">
                     
             
          </p>
        </div>
        <div
          className={`swiper-slide${activeSlide === 5 ? " active" : ""}`}
          onMouseEnter={() => setActiveSlide(5)}
          onMouseLeave={() => setActiveSlide(3)}
        >
          <div className="swiper-green"></div>
          <h4 className="swiper-slide__title">
               
          </h4>
          <p className="swiper-slide__text">
                    
             
          </p>
        </div>
        <div
          className={`swiper-slide${activeSlide === 6 ? " active" : ""}`}
          onMouseEnter={() => setActiveSlide(6)}
          onMouseLeave={() => setActiveSlide(3)}
        >
          <div className="swiper-green"></div>
          <h4 className="swiper-slide__title"> </h4>
          <p className="swiper-slide__text">
                  
             
          </p>
        </div>
      </div>
      <Link to="/catalog">
        <button className="mainSwiper__button"> </button>
      </Link>
    </div>
  );
}
export default SwiperPage;
